* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background: #0d6dfa;
}
::-webkit-scrollbar-track {
    background: #ffffff;
}

.Homepage {
    width: 100%;
    height: 100vh;
    color: #ffffff;
    background-color: #202020;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}

.Navbar {
    background-color: #202020;
    opacity: 0.99;
    position: fixed;
    width: 100%;
    height: 64px;
}
.Content {
    display: flex;
    width: 100%;
    height: 64px;
    padding-left: 120px;
    padding-right: 120px;
    justify-content: space-between;
    align-items: center;
}
.ProjectName p {
    font-size: 1.4rem;
    margin: 0;
}
.NavLink {
    margin-right: 65px;
}
.LastNavLink {
    margin-right: none;
}

.MainBody {
    padding-top: 64px;
}
.ProjectInfo {
    text-align: center;
}
.ProjectInfo h2 {
    font-size: 4.75rem;
    line-height: 75px;
    font-weight: 600;
    margin-top: 7%;
}
.ProjectInfo h4 {
    color: #8f8f8f;
    font-size: 1.3rem;
    line-height: 30px;
    margin-top: 2%;
}

